.title {
    font-size: 23px;
    font-weight: 700;
    margin-top: 1em;
    margin-bottom: 0.5em;
}

.daily-item {
    background-color: #f5f5f5;
    border-radius: 15px;
    height: 40px;
    margin: 5px;
    align-items: center;
    cursor: pointer;
    display: flex;
    font-size: 14px;
    padding: 5px 20px;
}

.daily-item:hover {
    height: 50px;
    font-size: 16px;
    background-color: #e3dbdb;
}

.icon-small {
    width: 40px;
    display: block;
}

.daily-item .day {
    cursor: inherit;
    color: #212121;
    flex: 1 1;
    font-weight: 600;
    margin: 15px;
}

.description {
    cursor: inherit;
    flex: 1 1;
    margin: 15px;
    text-align: right;
    display: block;
    text-align: center;
}

.min-max {
    color: #757575;
    display: block;
}

.daily-details-grid {
    border-radius: 1em;
    background-color: #f5f5f5;
    grid-row-gap: 0;
    grid-column-gap: 15px;
    -webkit-column-gap: 15px;
    column-gap: 15px;
    display: grid;
    flex: 1 1;
    grid-template-columns: auto auto;
    padding: 5px 15px;
    row-gap: 0;
}

.daily-details-grid-item {
    align-items: center;
    display: flex;
    height: 30px;
    justify-content: space-between;
}

.daily-details-grid-item label:first-child {
    color: #757575;
}

.daily-details-grid-item label:last-child {
    color: #212121;
}

@media only screen and (max-width : 500px) {
    .min-max {
        display: block;
        margin: 0 15px;
    }

    .description {
        display: none;
    }

    .icon-small {
        display: block;
    }

}

@media only screen and (max-width : 300px) {

    .icon-small {
        display: none;
    }
}